import React from "react";
import { useSelector } from "react-redux";
import Aboutx4check from "../../components/about-bob/about-bob";
import AboutUs from "../../components/about-us/about-us";
import ContactUs from "../../components/contact/contact";
import Footer from "../../components/footer/footer";
import Header from "../../components/header/header";
import IntroAiSection from "../../components/intro-ai/intro-ai";

import Profile from "../../components/profile/profile";
import AiSec from "../../components/profile/profile-ai";
import Plagi from "../../components/profile/profile-plagiat";
import Summar from "../../components/profile/profile-sum";

import WorkSec from "../../components/work-sec/work-sec";
import { useMediaQuery } from "react-responsive";
import HeaderMobile from "../../components/header-mob/header";
import ExtanderPage from "../extander/extander";
import Extand from "../../components/profile/profile-ext";

const LandingPage = () =>{
    const isMobile = useMediaQuery({
        query: '(max-width: 991px)'
      })
    const {textDeco} = useSelector(state => state.toggle)
    console.log(textDeco);
    return(
        <>
        {isMobile ? <HeaderMobile /> :      <Header></Header>}
        
            {textDeco === "aiWriter" && <AiSec />}
            {textDeco === "grammarcheck" && <Plagi />}
            {textDeco === "summarizer" && <Summar />}
            {textDeco === "rewriter" && <Profile />}
            {textDeco === "x4check_chat" && <Extand />}
            <IntroAiSection></IntroAiSection>
            <AboutUs></AboutUs>
            <WorkSec></WorkSec>
            <Aboutx4check></Aboutx4check>
            <ContactUs></ContactUs>
            <Footer></Footer>
        </>
    )

}

export default LandingPage;