import React, { useEffect, useRef } from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { fetchMode } from "../../redux/modeSlice";


import './style.css'
import { useNavigate } from "react-router-dom";
import LeftBoxTextExt from "../ext/left-text/left-text";
import RightBoxTextExt from "../ext/right-text/right-text";
import { settextDeco } from "../../redux/varDataSlice";
import { useMediaQuery } from "react-responsive";
const Extand= () =>{
    const [modes,setMode] = useState('normal')
    let dispatch = useDispatch()

    function setModeFun(mode,i){
        let lists = document.querySelectorAll('.navbar-items-text')
        Array.from(lists).map((x,index)=>{
            if(i==index)
            lists[index].classList.add('isActive')
            else
            lists[index].classList.remove('isActive')

        })
        dispatch(fetchMode(modes))
    }
    
    const navigate = useNavigate()
    let token = localStorage.getItem('token')

    function handleText(val) {
        dispatch(settextDeco(val))
        navigate('/')
    }
    function changeVal(x){
        console.log(x);
        if(!token){
            dispatch(settextDeco(x))
        navigate('/')
        }else{
            navigate('/'+x)
        }
    
    }
    const isMobile = useMediaQuery({
        query: '(max-width: 991px)'
      })
    const curr = useRef()
    function handleClick(){
        curr.current.scrollIntoView({ behavior: 'smooth' })
    }

    return(
        <div className="profile-container-box">
            <div className="width-profile-text-container" style={{width:"100%!important",position:"relative"}}>
            {
                        isMobile && 
                            <div className="icon-go-down" onClick={handleClick}>
                            <i class="fa-solid fa-arrow-down"></i>    
                            </div>   
                     }
                <div className="button-check-text-mobile">
                    <div className="pos-angle-list">
                    <i class="fa-solid fa-angle-down"></i>
                    </div>
                    <select className="paraphrase-type" onChange={e => changeVal(e.target.value)}>
                                                <option value="">CHOSE YOUR TOOL</option>

                        <option value="aiWriter" >AI TEXT</option>
                        <option value="rewriter">REWRITER</option>
                        <option value={!token ? "grammarcheck" :"gramma-check"}>GRAMMA CHECK</option>
                        <option value="summarizer">SUMMARIZE</option>
                        <option value="x4check_chat">Askx4</option>
                    </select>
                </div>
                <div className="text-profile-container"  style={{width:"100%!important"}}>
                    <div className="main-box-text ">
                        <div className="navbar-box-text">
                            <div className="list-chice-box">

                                <ul className="list-choice-tone">
                                    <li className="navbar-items-text-desc" >Chat with 4xCheck AI </li>
                                    
                                </ul>
                            </div>
                           
                        </div>
                        <div className="navbar-box-text-mb">
                            <div className="list-chice-box-mb">
                                <div className="btn-select-type-lang">
                                <div className="pos-angle-list-lang">
                                    <i class="fa-solid fa-angle-down"></i>
                                    </div>
                                    <select name="" className="type-btn">
                                        <option value="en">English</option>
                                
                                    </select>
                                </div>

                            </div>

                        </div>
                        <div className="box-rewrite-tool">
                            <div className="container-rewrite-tool">
                                <LeftBoxTextExt></LeftBoxTextExt>
                            </div>
                            <div className="container-rewrite-tool" ref={curr}>
                                <RightBoxTextExt></RightBoxTextExt>
                            </div>
                            <div className="img-rand-pos">
                                <img src={process.env.PUBLIC_URL+'/img/rand.png'} className="img-text-rand"/>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="text-about">
                    <div className="row g-0 justify-content-between align-items-center">
                        <div className="col-12 col-lg-6">
                            <div className="text-desc-box-rew">
                                <p className="text-box-rew">
                                Askx4
                                </p>
                            </div>
                        </div>
                        <div className="col-12 col-lg-4">
                        <div className="row justify-content-center">
                                    <div className="col-12 col-lg-12 ">

                                            <div className="row justify-content-center align-items-center border-img-sec">
                                                <div className="col-12 col-lg-5 d-flex justify-content-center d-lg-flex justify-content-end">
                                                    <div className="">
                                                        <img src={process.env.PUBLIC_URL+'/img/lamp.png'} />
                                                    </div>
                                                </div>
                                                <div className="col-12 col-lg-7 ">
                                                <div className="border-img-secs">
                                                    <p className="text-about-ai">
                                                    Have some fun with it!. Discover how
                                                    x4check can enhance your writing
                                                    within seconds.
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )


}

export default Extand;
