import React from "react";
import './style.css';
import Footer from "../../components/footer/footer";
import HeaderMobile from "../../components/header-mob/header";
import Header from "../../components/header/header";
import { useMediaQuery } from "react-responsive";

const PrivacyTerms = () => {

    const isMobile = useMediaQuery({
        query: '(max-width: 991px)'
    })

    return (
        <div className="">
            {isMobile ? <HeaderMobile /> : <Header></Header>}
            <div className="container-custom">
                <div className="margin-privacy">

                    <h1>Privacy Policy</h1>

                    <p>This Privacy Policy outlines how we collect, use, disclose, and safeguard your personal information when you use the AI-Based, Free Article Rewriting Tool (referred to as "the Tool"). By using the Tool, you consent to the practices described in this policy.</p>

                    <h2>1. Information Collected</h2>
                    <ol>
                        <li>
                            <strong>User-Provided Information:</strong>
                            When using the Tool, you may provide information such as text content for rewriting, chat interactions, and contact details.
                        </li>
                        <li>
                            <strong>Automatically Collected Information:</strong>
                            We may collect information automatically, including but not limited to IP addresses, device information, usage patterns, and other analytics data.
                        </li>
                    </ol>

                    <h2>2. Use of Collected Information</h2>
                    <ol>
                        <li>
                            <strong>Tool Functionality:</strong>
                            User-provided information is used to perform the functionalities of the Tool, such as article rewriting, grammar checking, text generation, summarization, and chat interactions.
                        </li>
                        <li>
                            <strong>Improvement of Services:</strong>
                            We may use collected data to analyze usage patterns and improve the Tool's features, functionality, and user experience.
                        </li>
                        <li>
                            <strong>Communication:</strong>
                            We may use contact information to send important updates, notifications, or respond to user inquiries.
                        </li>
                    </ol>

                    <h2>3. Data Security</h2>
                    <p>We implement reasonable measures to protect user data from unauthorized access, disclosure, alteration, and destruction. However, no method of transmission over the internet or electronic storage is entirely secure, and we cannot guarantee absolute security.</p>

                    <h2>4. Sharing of Information</h2>
                    <p>We do not sell, trade, or rent user information to third parties. However, we may share aggregated, non-personal information for statistical purposes.</p>

                    <h2>5. Cookies and Tracking Technologies</h2>
                    <p>The Tool may use cookies and similar tracking technologies to enhance user experience. Users can control cookie preferences through their browser settings.</p>

                    <h2>6. Third-Party Links</h2>
                    <p>The Tool may contain links to third-party websites. We are not responsible for the privacy practices or content of these third-party sites. Users should review the privacy policies of these websites.</p>

                    <h2>7. Children's Privacy</h2>
                    <p>The Tool is not intended for use by individuals under the age of 13. We do not knowingly collect personal information from children. If you believe that a child has provided us with personal information, please contact us, and we will promptly delete such information.</p>

                    <h2>8. Changes to the Privacy Policy</h2>
                    <p>We reserve the right to modify this Privacy Policy at any time. Users will be notified of changes, and continued use of the Tool after modifications constitutes acceptance of the revised policy.</p>

                    <h2>9. Contact Information</h2>
                    <p>For questions, concerns, or requests regarding your personal information or this Privacy Policy, please contact us at <a href="mailto:X4check@marketer-solution.net">X4check@marketer-solution.net</a>.</p>
                </div>
            </div>



            <Footer></Footer>
        </div>
    )
}

export default PrivacyTerms;