import React from "react";
import './style.css';
import { useMediaQuery } from "react-responsive";

const WorkSec = () =>{
    const isMobile = useMediaQuery({
        query: '(max-width: 991px)'
      })
    return(
        <div className="container-custom">
<div className="work-sec-container mt-5">
            <div className="groups-dots-work">
                <img src={process.env.PUBLIC_URL+'/img/groups.png'} />
            </div>
            

                <div className="work-desc-container">
                <div className="title-work-sec">
                    <p className="title-work">Working with <span className="text-green">x4Check</span> is simple. </p>

            </div>

                    <div className="row my-4">
                        <div className="col-12 col-lg-6 mt-mb-5">
                            <div className="col-12 ">
                                <div className="d-flex justify-content-between box-shadow-t" style={{width:"100%"}}>
                                <div className="box-icon" style={{width:"10%"}}>
                                    <img src={process.env.PUBLIC_URL+'/img/text-a.png'} alt=""  style={{width:"30px",height:"30px"}}/>
                                </div>
                                <div className="" style={{width:"88%"}}>
                                    <p className="func-text">x4check Mastery</p>
                                    <p className="text-desc-work">
                                    Unlock Simplicity in Every Spin. We run every thing you can imagine.
                                    </p>
                                </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                    <div className={isMobile?  "row" : "row justify-content-center my-4  "} style={{width:"100%"}}>

                    <div className="col-12 col-lg-6 d-flex justify-content-center mt-mb-5">
                            <div className="col-12 d-flex">
                            <div className="d-flex justify-content-between box-shadow-t" style={{width:"100%"}}>
                            <div className="box-icon"  style={{width:"10%"}}>
                                    <img src={process.env.PUBLIC_URL+'/img/chart.png'} alt=""  style={{width:"30px",height:"30px"}}/>
                                </div>
                                <div className=""  style={{width:"88%"}}>
                                    <p className="func-text">Spin with Ease</p>
                                    <p className="text-desc-work">
                                        Navigating x4check for Effortless Content Transformation. It's very Easy to use.
                                    </p>
                                </div>
                            </div>
                               
                            </div>
                        </div>
                    </div>
                        
                        <div className={isMobile?  "row" : "row justify-content-end my-4 "}>
                        <div className="col-12 col-lg-6 d-flex justify-content-end  mt-mb-5">
                            <div className="col-12 d-flex">
                            <div className="d-flex justify-content-between box-shadow-t" style={{width:"100%"}}>
                            <div className="box-icon" style={{width:"10%"}}>
                                    <img src={process.env.PUBLIC_URL+'/img/check-all.png'} alt=""  style={{width:"30px",height:"30px"}}/>
                                </div>
                                <div className=""  style={{width:"88%"}}>
                                    <p className="func-text">Simplified Spinning</p>
                                    <p className="text-desc-work">
                                    Mastering x4check for Seamless Content Crafting.
                                    </p>
                                </div>
                            </div>

                                
                            </div>
                        </div>
                    </div>
                </div>

        </div>
        </div>
        
    )

}

export default WorkSec;