import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    textDeco : "rewriter"
}

export const toggleSlice = createSlice({
    name:"islogged",
    initialState,
    reducers:{
        settextDeco:(state,{payload})=>{
            state.textDeco = payload;
        }
    }
});

export const {settextDeco} = toggleSlice.actions;

export default toggleSlice.reducer;