import { useQuery } from "@tanstack/react-query";
import React, { useRef } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { getUserProfile } from "../../api/user.api";
import { setToken } from "../../redux/loggedSlice";
import { settextDeco } from "../../redux/varDataSlice";
import './style.css'
import { Box, Drawer } from "@mui/material";
import { useState } from "react";

const HeaderMobile = () => {
    
    const [state, setState] = React.useState({
        top: false,
        left: false,
        bottom: false,
        right: false,
    });
    
    const [inSettings,setInSettings] = useState(null);

    const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setState({ ...state, [anchor]: open });
    };

    const hiddenWidthRef = useRef(null)
    const shownWidthRef = useRef(null)

    function swipeRight() {

        hiddenWidthRef.current.style.display = 'none'
        shownWidthRef.current.style.display = 'block'
    }

    function swipeLeft() {

        hiddenWidthRef.current.style.display = 'block'
        shownWidthRef.current.style.display = 'none'

    }

    useEffect(() =>{
        
        const currentUrl = window.location.pathname;
        console.log(currentUrl);
        if(currentUrl == '/settings'){
            setInSettings(true)
        }else{
            setInSettings(false)
        }

    },[])
    
    function singout() {
        localStorage.removeItem('token')
        window.location.href = '/'
    }

    let dispatch = useDispatch()

    let token = localStorage.getItem('token')

    const { isLoading, data, isError, isFetching, refetch } = useQuery({
        queryKey: ['rewritse'],
        queryFn: async () => {
            const datas = await getUserProfile();
            return datas;

        }, refetchOnWindowFocus: false, enabled: true
    })

    const navigate = useNavigate();
    
    function handleText(val) {
        dispatch(settextDeco(val))
        navigate('/')
    }

    

    const list = (anchor) => (
        <Box
            sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : "100vw", height: "100vh", background: "rgba(40, 141, 105, 0.13)" }}
            role="presentation"

        >
            <div className="header-box">
                <div className="d-flex justify-content-between align-items-center px-4">
                    <div className="logo-sec-header">
                        <img src={process.env.PUBLIC_URL + '/img/logo.png'} alt="" className="log-nav-img" />
                    </div>
   
                    <div className="color-exit" onClick={toggleDrawer("right", false)}>
                    <i class="fa-solid fa-2x fa-xmark"></i>
                    </div>
                    
                </div>
                {token ? <>
                    {inSettings  ?
                   <div className="navbar-text-mob">
                   <ul className="ul">
                       <li className="list-rom" onClick={e => handleText("rewriter")}>Rewriter</li>
                       <li className="list-rom" onClick={e => handleText("aiWriter")}>AI Text</li>
                       <li className="list-rom" onClick={e => handleText("grammarcheck")}>Gramma Check</li>
                       <li className="list-rom" onClick={e => handleText("summarizer")}>Summarizer</li>
                       <li className="list-rom" onClick={e => handleText("x4check_chat")}>Askx4</li>
                       

                   </ul>
               </div>
                : 
                   <div className="navbar-text-mob">
                   <ul className="ul">
                       <li className="list-rom" onClick={e => navigate("/rewriter")}>Rewriter</li>
                       <li className="list-rom" onClick={e => navigate("/aiWriter")}>AI Text</li>
                       <li className="list-rom" onClick={e => navigate("/gramma-check")}>Gramma Check</li>
                       <li className="list-rom" onClick={e => navigate("/summarizer")}>Summarizer</li>
                       <li className="list-rom" onClick={e => navigate("/x4check_chat")}>Askx4</li>

                   </ul>
               </div>
                }

                </> : <>
                <div className="navbar-text-mob">
                   <ul className="ul">
                       <li className="list-rom" onClick={e => handleText("rewriter")}>Rewriter</li>
                       <li className="list-rom" onClick={e => handleText("aiWriter")}>AI Text</li>
                       <li className="list-rom" onClick={e => handleText("grammarcheck")}>Gramma Check</li>
                       <li className="list-rom" onClick={e => handleText("summarizer")}>Summarizer</li>

                   </ul>
               </div>
                </>}
             
                {
                    !token ?
                        <div className="btn-box">
                            <div className="divider-rw"></div>


                            <div className="my-4 px-3">
                                <Link to="/register" className="button-re mt-3">Register</Link>
                                <Link to="/login" className="button-re-ln mt-3">Login</Link>
                            </div>      
                            </div>
                        :
                        <div className="btn-box">
                            <div className="divider-rw"></div>
                            <div className="d-flex justify-content-between align-items-center p-3" style={{width:"100%"}}>
                                <div className="d-flex">
                                <img src={"https://x4check.com/public/images/" + data?.picture} alt="" className="user-profile-img" />
                                <div class="dropdown">
                                    <button class="ms-2 dropdown-toggle" style={{ background: "transparent", border: "none", outline: "none", textTransform: "capitalize", color: "#0E243A" }} type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        {data?.username}
                                    </button>
                                    <ul class="dropdown-menu mt-3 p-0">
                                        <li className="p-3 id-list-nav"><Link to="/settings" style={{ textTransform: "capitalize", color: "#0E243A" }}>Settings</Link></li>
                                        <li className="p-3 id-list-nav"><Link onClick={singout} to={'/'} style={{ textTransform: "capitalize", color: "#0E243A" }}>Sing out</Link></li>
                                    </ul>
                                </div>
                                    </div> 
                                    <div className="color-exit" onClick={singout}>
                                                <i class="fa-solid fa-2x fa-right-from-bracket"></i>
                                            </div> 
                              
                            </div>
                        </div>
                }


            </div>
        </Box>
    );

    return (
        <div className="Header-sec">
            <div className="navar-header py-4">
                <div className="container-custom">
                    <div className="row g-0 justify-content-between align-items-center">
                        <div className="col-6">
                            <div className="logo-sec">
                                <img src={process.env.PUBLIC_URL + '/img/logo.png'} alt="" className="log-nav-img" />
                            </div>
                        </div>

                        <div className="col-6 d-flex justify-content-end">

                            <div className="info-sec">
                                {
                                    !token ?

                                    <div className="d-flex align-items-center">

                                    <div className="up-input d-blcok">
                                        <button className="up-btn-nav" onClick={toggleDrawer("right", true)} >Get started</button>
                                    </div>

                                </div> 
                                        :
                                        <div className="d-flex align-items-center">

                                        <div className="up-input d-blcok">
                                            <button className="up-btn-nav" onClick={toggleDrawer("right", true)} >Settings</button>
                                        </div>

                                    </div> 
                                }

                            </div>

                            <Drawer
                                anchor={"right"}
                                open={state["right"]}
                                onClose={toggleDrawer("right", false)}
                            >
                                {list("right")}
                            </Drawer>

                        </div>
                    </div>
                </div>

            </div>

        </div>
    )

}
export default HeaderMobile;