import { useQuery } from "@tanstack/react-query";
import React, { useRef } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { getUserProfile } from "../../api/user.api";
import { setToken } from "../../redux/loggedSlice";
import { settextDeco } from "../../redux/varDataSlice";
import './style.css'
import { useState } from "react";
const Header = () =>{

    const hiddenWidthRef = useRef(null)
    const shownWidthRef = useRef(null)
    const [inSettings,setInSettings] = useState(null);

    function swipeRight(){
    
        hiddenWidthRef.current.style.display='none'
        shownWidthRef.current.style.display='block'
    }

    function swipeLeft(){
        
        hiddenWidthRef.current.style.display='block'
        shownWidthRef.current.style.display='none'

    }

    useEffect(() =>{
        
        const currentUrl = window.location.pathname;
        console.log(currentUrl);
        if(currentUrl !== '/'){
            setInSettings(true)
        }else{
            setInSettings(false)
        }

    },[])
    function singout(){
        localStorage.removeItem('token')
        window.location.href='/'
    }

    let dispatch = useDispatch()
    
    let token = localStorage.getItem('token')

    const {isLoading,data,isError,isFetching,refetch} = useQuery({
        queryKey:['rewritse'],  
        queryFn : async ()=>{
        const datas = await getUserProfile();
        return datas;
    
},  refetchOnWindowFocus: false,enabled:true})

const navigate = useNavigate()
const [url,setURL] = useState('')
const {textDeco} = useSelector(state => state.toggle);
useEffect(()=>{
    setURL( window.location.pathname)
},[]);

useEffect(()=>{
    console.log(textDeco);
},[textDeco]);

function handleText(val){
    dispatch(settextDeco(val))
    navigate('/')
}
    

    return(
        <div className="Header-sec">
            <div className="navar-header py-4">
                <div className="container-custom">
                <div className="row justify-content-between align-items-center">
                        <div className="col-2">
                        <div className="logo-sec">
                            <Link to="/"><img src={process.env.PUBLIC_URL+'/img/logo.png'} alt="" className="log-nav-img" /></Link>
                        </div>
                        </div>
                        <div className="col-9 d-flex justify-content-between align-items-center">
                        <div className="d-flex justify-content-center align-items-center"  style={{width:"70%"}}>
                            { inSettings ?  
                            <div style={{width:"100%"}}>

                               <ul className="d-flex justify-content-between align-items-center " style={{gap:"15px"}}>

                               <li><button className={url == "/rewriter" ? "btn-text btn-text-active":"btn-text"} onClick={e => navigate("/rewriter")}>Rewriter</button></li>
                               <li><button className={url == "/aiWriter" ? "btn-text btn-text-active":"btn-text"} onClick={e => navigate("/aiWriter")}>Ai Text</button></li>
                               <li><button className={url == "/gramma-check" ? "btn-text btn-text-active":"btn-text"} onClick={e => navigate("/gramma-check")}>Gramma Check</button></li>
                               <li><button className={url == "/summarizer" ? "btn-text btn-text-active":"btn-text"} onClick={e => navigate("/summarizer")}>Summarizer</button></li>
                               <li><button className={url == "/x4check_chat" ? "btn-text btn-text-active":"btn-text"} onClick={e => navigate("/x4check_chat")}>Askx4</button></li>
                            </ul>
                           </div>
                           :
                           <div style={{width:"100%"}}>
                                <ul className="d-flex justify-content-between align-items-center " style={{gap:"15px"}}>
                               <li><button className={textDeco == "rewriter" ? "btn-text btn-text-active":"btn-text"} onClick={e => handleText("rewriter")}>Rewriter</button></li>
                               <li><button className={textDeco == "aitext" ? "btn-text btn-text-active":"btn-text"} onClick={e => handleText("aiWriter")}>Ai Text</button></li>
                               <li><button className={textDeco == "grammarcheck" ? "btn-text btn-text-active":"btn-text"} onClick={e => handleText("grammarcheck")}>Gramma Check</button></li>
                               <li><button className={textDeco == "summarizer" ? "btn-text btn-text-active":"btn-text"} onClick={e => handleText("summarizer")}>Summarizer</button></li>
                               <li><button className={textDeco == "x4check_chat" ? "btn-text btn-text-active":"btn-text"} onClick={e => handleText("x4check_chat")}>Askx4</button></li>
                                </ul>
                            </div>
                            }
                          
                          
                        </div>
                        <div className="info-sec">
                            {
                                !token  ?
                                
                                <div className="d-flex align-items-center">
  
                                    <div className="up-input d-blcok">
                                        <Link to='/register' className="up-btn-nav" >Get started</Link>
                                    </div>

                                </div> : 
                                <div className="d-flex align-items-center">
   
                                <div className="up-input me-3 d-blcok">
                                    <div className="">

                                    <img src={process.env.PUBLIC_URL+'/img/Shapenot.png'} alt=""  />
                                    </div>
                                </div>
                                <div className="d-flex align-items-center">
                                <img src={"https://x4check.com/public/images/"+data?.picture} alt="" className="user-profile-img" />
                                    <div class="dropdown">
                                        <button class="ms-2 dropdown-toggle" style={{background:"transparent",border:"none",outline:"none",textTransform:"capitalize",color:"#0E243A"}} type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        {data?.username}
                                        </button>
                                        <ul class="dropdown-menu mt-3 p-0">
                                            <li className="p-3 id-list-nav"><Link to="/settings"  style={{textTransform:"capitalize",color:"#0E243A"}}>Settings</Link></li>
                                            <li className="p-3 id-list-nav"><Link onClick={singout}  to={'/'} style={{textTransform:"capitalize",color:"#0E243A"}}>Sing out</Link></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        }
                            
                        </div>
                        </div>
                        
                    </div>
                </div>
       
            </div>

        </div>
    )

}
export default Header;